import header from '@/components/header';
import emptyTips from '@/components/emptyTips';
export default {
  props: {
    userId: {
      Number,
      default: 0
    },
    userType: {
      Number,
      default: 0
    }
  },
  data() {
    return {
      showTips: 0,
      mailBoxList: [],
      titleContent: '园长邮箱',
      defaultAvatar: 'http://cdn-didano.oss-cn-shenzhen.aliyuncs.com/xiaonuo/wx/img/logo_2.jpg'
    }
  },
  beforeCreate () {
    let currentUser = JSON.parse(localStorage.getItem('CURRENT_USER'));
    if( currentUser.roleType === PARENT) {
      this.$router.push('/mailContent');
    }
  },
  created() {
    let currentUser = JSON.parse(localStorage.getItem('CURRENT_USER'));
    if( currentUser.roleType === PARENT) {
      this.$router.push('/mailContent');
    } else {

      this._initMailBox();
    }
  },
  methods: {
    _initMailBox() {
      this.$http.post(`${WX_URL}/base/mailbox/post/mail_findtByBossId/${this.userId}/${this.userType}`).then((response) => {
        response = response.data;
        if( response.success ) {
          this.mailBoxList = response.data;
          if (!this.mailBoxList.list.length) {
            this.showTips = 1;
          } else {
            this.showTips = 0;
          }
        }
      })
    },
    // 消息标志为已读
    isRead(noticeId,index) {
      this.$http.post(`${WX_URL}base/notice/post/setNoticeRead`,{'noticeId':noticeId,'userId':this.userId}).then((response) => {
        response = response.data;
        if(response.success){
          // 修改本地列表中对应消息的未读状态修改成为已读
          this.mailBoxList.list[index].is_read = 1;
        }else{
            //消息未成功标志为已读状态码：1002
            console.log("code:1002");
        }
      });
    },
    selectMail(mailItem,index) {
      // 将未读消息标志为已读
      if (!mailItem.is_read) {
        this.isRead(mailItem.id,index);
      }
      this.$router.push({ path: '/mailContent', query: { mailId: mailItem.sourceId, dialogueTitle:`与${mailItem.title}进行沟通` }});
    }
  },
  components: {
    'v-header': header,
     emptyTips
  }
}



